import React from 'react';
import {BrowserRouter as Router,Redirect,Route,NavLink,Switch} from 'react-router-dom'
import {Provider} from 'react-redux';
import Home from './Home'
import Cart from './Cart'
import Mytab from './Mytab';
import Order from './Order';
import Mine from './Mine';
import SearchBarExample from './SearchBarExample'
import Detail from './Detail'
import Colletion from './Colletion';
import Shop from './Shop';
import Servicer from './Servicer';
import Buys from './Buys';
import store from './store';
import Login from './Login'

//获取数据 id 名字 图片 价格
//详情页，

const App = ()=>{
    return <Provider store={store}>
        <Router>
            <Switch>
                <Route exact path='/home' component={Home}/>
                {/* <Route path='/cart' component={Cart}/> */}
                <Route path='/order' component={Order}/>
                <Route path='/mine' component={Mine}/>
                <Route path='/home/search' component={SearchBarExample}/>
                <Route path='/home/details' component={Detail}/>
                <Route path='/details/shop' component={Shop}/>
                <Route path='/details/servicer' component={Servicer}/>
                <Route path='/colletion' component={Colletion}/>
                <Route path='/details/buys' component={Buys}/>
                <Route path='/login' component={Login}/>
                {/* 开始进入,404等跳进首页 */}
                {/* <Route component={Home}/> */}
                <Route render={()=><Redirect to='/home'/>}/>           
            </Switch>      
            <Mytab/>
        </Router>
    </Provider>
}
export default App;