import React from 'react'
import { connect } from 'react-redux'
const Colletion = (props) => {
    console.log(props);
    return ( 
        <div>                
           {          

            props.collection.map((item,idx)=>{
                
                return(
                    
                    <div className="shop1" key={idx}>
                        <img src={item[0]} alt="#" className='shouimg'

                        style={{width:'200px',height:'250px'}}
                        />
                        <div className='shan'>
                            <button onClick={()=>{
                                props.dispatch({
                                    type:'DELETE_SC',
                                    index:idx
                                })
                            }}>
                            取消收藏
                            </button>
                        </div>
                    </div>               
                )
            })
          }
        </div>            
    
    )    
    
}

const mapStateToProps = (state) => ({
    datalist: state.content,
    collection:state.collection
})
export default connect(mapStateToProps)(Colletion);