import React from 'react';
import {Carousel} from 'antd-mobile';
import {withRouter} from 'react-router-dom'
import './taobao.css'

class Carousels extends React.Component {
    state = {
      data: [1,2,3,4,5,6],
      imgHeight: 198,
    }
    componentDidMount() {
      setTimeout(() => {
        this.setState({
          // data:['01','02','03','04','05','06']
          data:[
            'https://gw.alicdn.com/imgextra/i4/127/O1CN01Rs6Ndf1CoCvkTdGGC_!!127-0-lubanu.jpg',
            'https://gw.alicdn.com/imgextra/i1/15/O1CN01rsCXLl1ByuZUQlSMm_!!15-0-lubanu.jpg',
            'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg',
            'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg',
            'https://gw.alicdn.com/imgextra/i3/74/O1CN01UY7Tlw1CPvwP1Rdkx_!!74-0-lubanu.jpg',
            'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i2/6000000006904/O1CN01dLPgGK20s54ErU225_!!6000000006904-0-octopus.jpg'
          ]
        })
      }, 3000);
    }
   
    render() {
      return (
        <div className='roul'>
          <Carousel
            autoplay
            infinite
          >
          {this.state.data.map(val => (
            <a
              key={val}
              href="http://www.alipay.com"
                style={{               
                width: '100%',
                display: 'inline-block', 
                height: this.state.imgHeight 
              }} 
            >          
              <img
                // src ={require('../assets/'+`${val}`+'.jpg')}
                src ={`${val}`}
                alt=""
                style={{ width: '100%',height:'200',verticalAlign:'top'}}
                onLoad={() => {
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a> 

          ))}                   
          </Carousel>  
      </div>            
    );   
  }
}

export default withRouter(Carousels);