import React from 'react'
import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';
import './taobao.css';
import Carousels from './Carousels';
import Content from './Content';
//icfont.cn 官网搜选图表，加入购物车，点击页面右上角购物车，将图标添加进项目，
//点击下载本地，解压压缩包，全部复制到项目public文件夹下
//在index.html中通过link引入iconfont.css
//使用时<i className='iconfont icon-具体名字'></i>
class Home extends React.Component { 
    
    render(){
        return (
            <div>     
                <div className='top'>   
                    <SearchBar 
                       className = "iconfont icon-tao1"             
                        style={{ 
                            backgroundColor:' rgb(255, 133, 42)',                         
                            fontSize:'28px',
                            paddingLeft:'6px'
                        }}                        
                        placeholder="寻找宝贝店铺" 
                        onFocus={()=>this.props.history.push('/home/search')}
                    />
                </div>  
                
                <Carousels/> 
          
                <WhiteSpace size='lg'/>               
                    <img
                     src="https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png"
                     style={{
                         width:'40%',
                         paddingLeft:'28%'
                    }}
                    />                   
                <WhiteSpace size='md'/>
               

                <div className="contents">
                    <Content history ={this.props.history} />
                </div>
                
            </div>
        )
    }  
}
   

export default Home