import React, { useEffect } from 'react'
import { render } from 'react-dom'
import { connect } from 'react-redux'
import {datas} from './actionCreators'
import store from './store'

const Content = (props) => {
    useEffect(() => {
        store.dispatch(datas())
    }, [])
     
    return(
      <div>           
         {
            Array.from(props.datalist).map(item=>{
                return(                  
                    <div className="con"
                        onClick={()=>{                   
                            props.history.push('/home/details',[item.img,item.id]);              
                        }}
                    >                   
                        <img src={`${item.img}`}/>
                        <p 
                            style={{
                                fontSize:'13px',
                                margin:'9px 13px',
                                lineHeight:'22px',
                                height:'44px',
                                width:'150px',
                                overflow:'hidden'
                            }}>
                            {item.title}
                        </p>
                        <p style={{marginLeft:'13px'}}>
                            <span style={{color:'#ff5500',fontSize:'15px'}}>￥{item.price}</span>
                            &nbsp;&nbsp;
                            <span style={{fontSize:'11px',fontWeight:'lighter'}}>
                                {item.id}人付款
                            </span>                   
                        </p>
                    </div>            
                )  
            })  
          }
        </div>
                                
    )  
  
}
const mapStateToProps = (state) => ({
    datalist: state.content
})

export default connect(mapStateToProps)(Content);