import React, { Component } from 'react';
import Input from './Input';
import List from './List';


class TodoList extends Component {
    
constructor(){
    super();
    this.state = {
        todos:[],
        inpValue:''
    }
}  
componentDidMount(){
    // this.input.focus();
    let todos = localStorage.getItem('todos');
    if(todos){
        this.setState({
            todos: JSON.parse(todos)
        })
    }
}
componentDidUpdate(preProps,preState){
    console.log('123');
    if(preState.todos!==this.state.todos){
        localStorage.setItem(
            'todos',
            JSON.stringify(this.state.todos)
        )
    }
}
addTodo = (inpValue)=>{
    if(inpValue==''){
        return;
    }
    this.setState({
        todos: [
            {
                title: inpValue,
                done: false
            },
            ...this.state.todos
        ]
    })
}
delTodo = (idx)=>{
    // let newTodos = JSON.parse(JSON.stringify(this.state.todos))
    // newTodos.splice(idx,1);
    // this.setState({todos:newTodos});
    this.setState({
        todos: this.state.todos.filter((item,index)=>index!=idx)
    });
    // setState 是异步执行的，注意callback的用法
    // this.setState({
    //     todos: this.state.todos.filter((item,index)=>index!=idx)
    // },()=>{
    //     localStorage.setItem(
    //         'todos',
    //         JSON.stringify(this.state.todos)
    //     )
    // });
}
toggle = (index)=>{
    let newTodos = JSON.parse(JSON.stringify(this.state.todos))
    newTodos[index].done = !newTodos[index].done;
    this.setState({
        todos: newTodos
    })
} 
handleChange = (e)=>{
    this.setState({inpValue:e.target.value})
}
render() {
    return (
        <div className="box">
            {/* <input type="text" onChange={this.handleChange}/> */}
            <Input addTodo={this.addTodo}>
                <h1>todolist</h1>
                <div>
                    <p>hehe</p>
                </div>
                </Input>
            <List
                toggle={this.toggle} 
                delTodo={this.delTodo} 
                todos={this.state.todos}
            />
        </div>
    );
}
}


export default TodoList;
