import React from 'react';
import {Button,TabBar} from 'antd-mobile'
import {withRouter} from 'react-router-dom'
import { connect } from 'react-redux'
class Mytab extends React.Component {
	constructor(props) {
	  super(props);
	  this.state = {
		selectedTab: 'blueTab',
	  };
	}	
	render() {
		const {pathname} = this.props.location;
		console.log(pathname);
		if(pathname.split('/').length>2){
			return null;
		}
		return (
			// <div style={{ position: 'fixed!important', width: '100%', bottom: 0 }}>
			<div style={{ position: 'fixed', width: '100%', bottom: 0 }}>
	
			<TabBar
				unselectedTintColor="#949494"
				tintColor="grey"
				barTintColor="white"
			>
				<TabBar.Item
					title="首页"
					key="home"
					icon={
						<i style={{fontSize:22}} className='iconfont icon-Homehomepagemenu'></i> 
					
					}
					selectedIcon={
					<i style={{fontSize:22}} className='iconfont icon-Homehomepagemenu1'></i> 
					}
					selected={pathname === '/home'}
					//   badge={1}
					onPress={() => {
						// this.setState({
						//   selectedTab: 'blueTab',
						// });
						this.props.history.push('/home');

					}}
					data-seed="logId"
					>
				</TabBar.Item>

				<TabBar.Item				
					icon={
						<i style={{fontSize:22}} className='iconfont icon-icon4'></i> 
					}
					selectedIcon={
						<i style={{fontSize:22}} className='iconfont icon-htmal5icon29'></i> 
					}
					title="收藏"
					key="Koubei"
					//   badge={'new'}
					selected={pathname === '/colletion'}
					onPress={() => {
						console.log(this.props);                      
                        if (this.props.username) {   
							this.props.history.push('/colletion')
						}else { this.props.history.push('/login',this.props.idx) }
					}}
					data-seed="logId1"
					>
						{/* <Button onClick={()=>{
							this.props.history.push('/doc')
						}} type='primary'>ToDoc</Button> */}
					
				</TabBar.Item>

				<TabBar.Item
					icon={
						<i style={{fontSize:22}} className='iconfont icon-dingdan_huaban'></i> 
					}
					selectedIcon={
						<i style={{fontSize:22}} className='iconfont icon-dingdan'></i> 
					}
					title="订单"
					key="dingdan"
					//   dot
					selected={pathname === '/order'}
					onPress={() => {
						this.props.history.push('/order')
					}}
					>
					我的
				</TabBar.Item>

				<TabBar.Item
					icon={
						<i style={{fontSize:22}} className='iconfont icon-daohanglan-05'></i> 
						}
					selectedIcon={  <i style={{fontSize:22}} className='iconfont icon-wode1'></i> }
					title="更多"
					key="my"
					selected={pathname === '/mine'}
					onPress={() => {
						this.props.history.push('/mine')
					}}
					>
					社区页
				</TabBar.Item>
			</TabBar>
			</div>
	   );
	}
  }

const mapStateToProps = (state) => ({
   username:state.login.userinfo,
})

export default withRouter(connect(mapStateToProps)(Mytab)); 
