import React from 'react'
import {withRouter} from 'react-router-dom'
import { Icon } from 'antd-mobile';
import './taobao.css'
import Lasttab from './Lasttab';
import { connect } from 'react-redux';
const Detail = (props) => {
    console.log(props);
    return <div className="details">          
            <div style={{width:'380px',height:'360px'}}>   
            <div>          
                <img 
                    style={{width:'100%',overflow:'hidden'}}
                    src={props.datalist[props.location.state[1]-100].img} 
                    alt=""
                /> 
                </div> 
               <Icon 
                    style={{          
                        position:'relative',
                        top:'-365px',
                        left:'11px',
                        color:'white',
                        backgroundColor:'black',
                        borderRadius: '100%',                       
                        opacity:'0.5',
                        
                    }} 
                    type="left" 
                    size="lg"    
                    onClick={()=>props.history.push('/home')}                    
                />              
            </div>                   
            <div className='last'>
                <Lasttab collection={props.location.state}/>
            </div>
        </div>

    
}
const mapStateToProps = (state) => ({
    datalist: state.content
})
export default withRouter(connect(mapStateToProps)(Detail));