import React from 'react';
import {Button,TabBar} from 'antd-mobile'
import {withRouter} from 'react-router-dom'
import { connect } from 'react-redux'
import './taobao.css'
class Lasttab extends React.Component {
	constructor(props) {
	  super(props);
	  this.state = {
        selectedTab: '',
        status:true
        // hidden: false,
        // fullScreen: false,
      };
      
      props.collection.map(item => {
            if (item == this.props.location.state[1]) {
                this.state = {
                    selectedTab: 'red',
                    status: false
                };
            }
        });
      
	}
	render() {
        // console.log(Boolean(this.props.id.indexOf(this.props.idx) + 1) ? true : this.state.selectedTab === 'blue');
		const {pathname} = this.props.location;
        console.log(pathname);
        
		return (
          <div className='tabs' style={{ position: 'fixed', width: '100%', bottom: 0 }}>	
			<div style={{width:'40%',float:'left'}}> 
                <TabBar                  
                    unselectedTintColor="#949494"
                    tintColor="grey"
                    barTintColor="white"
                >               
                    <TabBar.Item
                        title="店铺"
                        key="shop"
                        icon={
                            <i style={{fontSize:22}} className='iconfont icon-dianpu'></i> 
                        
                        } 
                        selectedIcon={
                        <i style={{fontSize:22}} className='iconfont icon-dianpu-tap'></i> 
                        }
                        selected={pathname === '/details/shop'}
                        onPress={() => {
                            this.props.history.push('/details/shop');

                        }}
                        data-seed="logId"
                        >				
                    </TabBar.Item>
                    
                    <TabBar.Item				
                        icon={
                            <i style={{fontSize:22}} className='iconfont icon-kefu'></i> 
                        }
                        selectedIcon={
                            <i style={{fontSize:22}} className='iconfont icon-htmal5icon29'></i> 
                        }
                        title="客服"
                        key="Kefu"
                        selected={pathname === '/details/servicer'}
                        onPress={() => {
                            this.props.history.push('/details/servicer')
                        }}
                        data-seed="logId1"
                        >
                    </TabBar.Item>

                    <TabBar.Item
                        icon={
                            <i style={{fontSize:22}} className='iconfont icon-shoucang1'></i> 
                        }
                        selectedIcon={
                            <i style={{fontSize:22}} className='iconfont icon-shoucang2'></i> 
                        }  
                        title="收藏"
                        key="collect"
                        selected={
                            //Boolean(this.props.id.indexOf(this.props.idx) + 1) ? true : this.state.selectedTab === 'blue'
                            this.state.status == false && this.state.selectedTab === 'red'

                        }
                        onPress={() => { 
                            console.log(this.props);
                            if(this.props.username == ''){
                                this.props.history.push({
                                    pathname:'/login',
                                })
                            }
                            else{
                                if (this.state.status == true) {
                                    this.props.dispatch({
                                        type: 'ADD_SC',
                                        collection: this.props.location.state,
                                    })
                                    this.setState({
                                        selectedTab: 'red',
                                        status: false
                                    });                                   
                                } else {
                                    this.props.dispatch({
                                        type: 'DELETE_SC',
                                        collection: this.props.location.state
                                    })
                                }
                                
                            }
                        }}    

                  >
                    </TabBar.Item>
			    </TabBar>
            </div>
            <div className='tabbox' style={{float:'left',width:'58%'}}>
               <div 
                  className='left' 
                  onClick={() => {
					this.props.history.push('/cart')
				   }}
                >加入购物车           
                </div>
               <div 
                 className='right'
                 onClick={() => {
                    this.props.history.push('/details/buys')
                 }}
               >立即购买</div>
            </div>            
		</div>           
	    );
	}
  }

const mapStateToProps = (state) => {
    console.log(state);
    return {username:state.login.userinfo,
    collection:state.collection}
}

  export default withRouter(connect(mapStateToProps)(Lasttab));