let collection = [];
const collectionreducer = (state = collection, action) => {
    if (action.type == 'ADD_SC') {
       
        if (state.indexOf(action.collectin) < 0) {
            return [...state, action.collection];
        }
        else {
            return state;
        }

    }
    if (action.type == 'DELETE_SC') {
        let arr = [...state];
        arr.splice(action.index,1);

        return arr
    }
    return state
}

export default collectionreducer;
